"use client";

import { useState } from "react";
import Link from "next/link";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { 
  CheckSquare, 
  Plus, 
  Calendar,
  AlertTriangle,
  Clock,
  ExternalLink 
} from "lucide-react";
import { 
  tasks, 
  getTasksByAssignee, 
  getProjectById,
  getTeamMemberById 
} from "@/lib/utils/dummy-data";
import { format } from "date-fns";

type TaskFilter = "recent" | "due_date" | "urgent" | "completed";

export function TasksPreview() {
  const [filter, setFilter] = useState<TaskFilter>("recent");
  const currentUserId = "u1"; // Mock current user
  
  const getUserTasks = () => {
    let userTasks = getTasksByAssignee(currentUserId);
    
    switch (filter) {
      case "recent":
        return userTasks
          .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
          .slice(0, 5);
      case "due_date":
        return userTasks
          .filter(t => t.dueDate)
          .sort((a, b) => new Date(a.dueDate!).getTime() - new Date(b.dueDate!).getTime())
          .slice(0, 5);
      case "urgent":
        return userTasks
          .filter(t => t.priority === "urgent" || t.priority === "high")
          .slice(0, 5);
      case "completed":
        return userTasks
          .filter(t => t.status === "completed")
          .sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
          .slice(0, 5);
      default:
        return userTasks.slice(0, 5);
    }
  };

  const filteredTasks = getUserTasks();

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "urgent":
        return "bg-red-500 text-white";
      case "high":
        return "bg-orange-500 text-white";
      case "medium":
        return "bg-yellow-500 text-white";
      case "low":
        return "bg-green-500 text-white";
      default:
        return "bg-gray-500 text-white";
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "completed":
        return "bg-green-100 text-green-800";
      case "in_progress":
        return "bg-blue-100 text-blue-800";
      case "todo":
        return "bg-gray-100 text-gray-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <CheckSquare className="h-5 w-5" />
            <CardTitle>My Tasks</CardTitle>
          </div>
          <div className="flex items-center space-x-2">
            <Select value={filter} onValueChange={(value) => setFilter(value as TaskFilter)}>
              <SelectTrigger className="w-40">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="recent">Recently Added</SelectItem>
                <SelectItem value="due_date">Due Date</SelectItem>
                <SelectItem value="urgent">Urgent</SelectItem>
                <SelectItem value="completed">Completed Recently</SelectItem>
              </SelectContent>
            </Select>
            <Button size="sm">
              <Plus className="h-4 w-4 mr-2" />
              Quick Create
            </Button>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {filteredTasks.length > 0 ? (
            filteredTasks.map((task) => {
              const project = getProjectById(task.projectId);
              const assignee = task.assignedTo ? getTeamMemberById(task.assignedTo) : null;
              
              return (
                <div
                  key={task.id}
                  className="flex items-center justify-between p-4 rounded-lg border hover:bg-accent/50 transition-colors"
                >
                  <div className="flex items-center space-x-4 flex-1">
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-1">
                        <h4 className="font-medium">{task.title}</h4>
                        <Badge className={`text-xs ${getPriorityColor(task.priority)}`}>
                          {task.priority}
                        </Badge>
                        <Badge variant="outline" className={getStatusColor(task.status)}>
                          {task.status.replace("_", " ")}
                        </Badge>
                      </div>
                      <div className="flex items-center space-x-4 text-sm text-muted-foreground">
                        <span>{project?.title}</span>
                        {task.dueDate && (
                          <div className="flex items-center space-x-1">
                            <Calendar className="h-3 w-3" />
                            <span>{format(new Date(task.dueDate), "MMM d")}</span>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    {assignee && (
                      <Avatar className="h-6 w-6">
                        <AvatarImage src={assignee.avatarUrl} />
                        <AvatarFallback className="text-xs">
                          {assignee.name.split(" ").map(n => n[0]).join("")}
                        </AvatarFallback>
                      </Avatar>
                    )}
                    <Button variant="ghost" size="sm" asChild>
                      <Link href={`/app/projects/${task.projectId}?task=${task.id}`}>
                        <ExternalLink className="h-3 w-3" />
                      </Link>
                    </Button>
                  </div>
                </div>
              );
            })
          ) : (
            <div className="text-center py-8">
              <CheckSquare className="h-8 w-8 text-muted-foreground mx-auto mb-2" />
              <p className="text-muted-foreground">No tasks found for this filter</p>
            </div>
          )}
        </div>
        
        <div className="flex justify-between items-center mt-6 pt-4 border-t">
          <Link href="/app/tasks">
            <Button variant="outline">
              View All Your Tasks
              <ExternalLink className="h-4 w-4 ml-2" />
            </Button>
          </Link>
          <Link href="/app/tasks/new">
            <Button>
              <Plus className="h-4 w-4 mr-2" />
              Quick Create Task
            </Button>
          </Link>
        </div>
      </CardContent>
    </Card>
  );
}